.App {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
}

form {
  margin-bottom: 20px;
}

input, textarea {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.note {
  background-color: #f9f9f9;
  border: 1px solid #eee;
  padding: 10px;
  margin: 10px 0;
}

.note h2 {
  margin: 0 0 10px;
}

.note button {
  margin-right: 10px;
}